var provider = {
  words_list: ["", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", ""],
  letters: '',
  title: function title() {
    return this.words(this.integer(2, 3));
  },
  sentence: function sentence() {
    return this.words(this.integer(3, 10)) + '.';
  },
  text: function text() {
    return this.sentences(this.integer(3, 6));
  },
  description: function description() {
    return this.sentences(this.integer(2, 5));
  },
  short_description: function short_description() {
    return this.sentence;
  },
  string: function string() {
    return this.words();
  },
  sentences: function sentences(n) {
    n = n || 3;
    var result = [];

    for (var i = 0; i < n; ++i) {
      result.push(this.sentence);
    }

    return result.join(' ');
  },
  word: function word() {
    return this.random_element(this.words_list);
  },
  words: function words(n) {
    return this.array_of_words(n).join(' ');
  },
  array_of_words: function array_of_words(n) {
    n = n || 7;
    var result = [];

    for (var i = 0; i < n; ++i) {
      result.push(this.word);
    }

    return result;
  },
  letter: function letter() {
    return this.random_element(this.letters);
  }
};
module.exports = provider;